import React from 'react'
import { useState, createElement } from 'react'
import Modal from './Modal'
import TaskFormSimple from './TaskFormSimple'
import Icon from './Icon'
import NotificationMaker from './NotificationMaker'

const Dashboard = ({xc, client}) => {
    console.log('render dash')
    const [showModal, setShowModal] = useState(false)
    const [modalContent, setModalContent] = useState(false)
    const show_Modal_with_TaskFormSimple = function() {
        const hide_Modal = () => {
            setModalContent(null)
            setShowModal(false)
        }
        const task_form_simple = createElement(
            TaskFormSimple,
            {xc: xc, handleCloseForm: hide_Modal}
        )
        setModalContent(task_form_simple)
        setShowModal(true)
    }
    const [container, setContainer] = useState()
    const [activeTab, setActiveTab] = useState('All')
    const display_task_Map = async function(map) {
        const results = await map()
        console.log(results)
        let formatted = ''
        results.forEach((value)=>{
            formatted = formatted.concat(JSON.stringify(value), '\n')
        })
        setContainer(formatted)
    }

    function NavItem({clickCallback, i, t, highlight_after_click=true}) {
        //console.log('render nav item')
        const handleClick = () => {
            if (highlight_after_click) {setActiveTab(t)}
            clickCallback()
        }
        const classString = (t===activeTab)? 'text-primary-emphasis' : 'link-secondary'
        return (
            <a onClick={handleClick} href='# '
            className={`nav-link ${classString} text-center text-decoration-none`}
            >
                <div className='fs-3'><Icon i={i}/></div>
                <div style={{fontSize: 'small'}}>{t}</div>
            </a>
        )
    }

    const dropdown = (
        <>
            <div class="btn-group dropup">
                <button type="button" class="btn btn-secondary dropdown-toggle" data-bs-toggle="dropdown" aria-expanded="false">
                    Dropup
                </button>
                <ul class="dropdown-menu">
                    <li><NavItem clickCallback={xc.signOut} i='bi-box-arrow-left' t='Sign Out'/></li>
                    <li><NotificationMaker port_to_sw={client.port_to_sw} /></li>
                </ul>
            </div>
        </>
    )
    
    return (
        <>
            <Modal xc={xc} showModal={showModal} setShowModal={setShowModal} modalContent={modalContent}/>
            <main className="overflow-hidden">{container}</main>
            <nav className='nav nav-underline nav-justified w-100 position-fixed bottom-0 bg-body-tertiary'>
                {dropdown}
                <NavItem clickCallback={()=>display_task_Map(xc.get_all_tasks)} i='bi-list-task' t='All'/>
                <NavItem clickCallback={()=>display_task_Map(xc.get_started_tasks)} i='bi-hourglass-bottom' t='Urgent'/>
                <NavItem clickCallback={show_Modal_with_TaskFormSimple} i='bi-file-earmark-plus' t='New Task' highlight_after_click={false}/>
            </nav>
        </>
    )
}

export default Dashboard;